    :topList="topList"
    :leftList="leftList"
    :topNavCheckState="topNavCheckState"
    :leftNavCheckState="leftNavCheckState"
    @changeState="changeState"
    :shadow="showShadow"
    :canDo="canDo"
    title="èµ„ç®¡ç³»ç»Ÿ"
  > -->
  <Money v-if="leftNavCheckState[0]" @toggle="setInvoice"></Money>
  <AccountView v-if="leftNavCheckState[1]"></AccountView>
  <AccountCenter v-if="leftNavCheckState[2]"></AccountCenter>
  <Invoice v-if="showInvoice" @toggle="setInvoice" />
  <!-- </Contanier> -->
</template>

<script>
import Contanier from "./container.vue";
import { ref } from "@vue/reactivity";
import AccountCenter from "./accountCenter.vue";
import AccountView from "./accountView.vue";
import Money from "./money.vue";
import Invoice from "./invoice.vue";
import query, { setQuery } from "../../../common/query";
export default {
  setup() {
    const topNavCheckState = ref([false, false, true]);
    const leftNavCheckState = ref([true, false, false]);
    const showInvoice = ref(false);
    const showShadow = ref(false);
    const canDo = ref(true);
    return {
      topNavCheckState,
      leftNavCheckState,
      topList: ["å·¥ä½œå°", "è§„åˆ™åè®®", "èµ„ç®¡ç³»ç»Ÿ"],
      leftList: ["èµ„é‡‘ç®¡ç†", "è´¦æˆ·æ€»è§ˆ", "è´¦æˆ·ä¸­å¿ƒ"],
      setButtonSize: "transform:scale(0.8);transform-origin:left center;",
      showInvoice,
      showShadow,
      canDo,
    };
  },
  created() {
    this.$bus.$on("setMoney", ({ index }) => {
      this.changeState({ left: true, index });
    });

    const index = +query().page;
    if (
      typeof index == "number" &&
      !isNaN(index) &&
      index >= 0 &&
      index < this.leftNavCheckState.length
    ) {
      this.leftNavCheckState.forEach(
        (r, i) => (this.leftNavCheckState[i] = false)
      );
      this.leftNavCheckState[index] = true;
    }
  },
  provide() {
    return {
      setButtonSize: this.setButtonSize,
      canDo: this.canDo,
    };
  },
  name: "money-e",
  components: {
    Contanier,
    Money,
    AccountCenter,
    AccountView,
    Invoice,
  },
  methods: {
    startVerify() {
      this.canDo = false;
    },
    successVerify() {
      this.canDo = true;
    },
    setInvoice() {
      this.showShadow = !this.showShadow;
      this.showInvoice = !this.showInvoice;
    },
    changeState({ top, left, index }) {
      if (top) {
        this.topNavCheckState.forEach((r, i) => {
          this.topNavCheckState[i] = false;
        });
        this.topNavCheckState[index] = true;
        setQuery({ tab: index });
      } else if (left) {
        this.leftNavCheckState.forEach((r, i) => {
          this.leftNavCheckState[i] = false;
        });
        this.leftNavCheckState[index] = true;
        setQuery({ page: index });
      }
    },
  },
};
</script>

