<script>
export default {
    created(){
    let flag=localStorage.getItem('__first_login_camptogo__')
    if(flag===null){
        this.isShow=true
        localStorage.setItem('__first_login_camptogo__','true')
        return
    }
    this.isShow=false
    },
    mounted(){
   let time =setInterval(()=>{
        if(this.timer>0){
            this.timer--
        }else{
            clearInterval(time)
        }
    },1000)
    },
    computed:{
        getTimer(){
            if(this.timer!==0){
                 return `(${this.timer}s)`
            }
            return ''
        },
        actived(){
            return this.timer===0
        }
    },
    data(){
        return{
            timer:5,
            isShow:false
        }
    },
    methods:{
        confirm(){
           if(this.timer!==0){return}
            this.isShow=false
        },
        close(){
             this.$router.push({path: "/tools/login"})
             this.isShow=false
        }
    }
}
</script>

