function query() {
  const strs = location.search.split("?");
  if (strs.length > 1) {
    return keyValue(strs[1]);
  } else {
    return {};
  }
}
function keyValue(str) {
  const res = {};
  str
    .split("&")
    .map((r) => r.split("="))
    .forEach((r) => {
      res[r[0]] = r[1];
    });
  return res;
}
export function setQuery(page = {}) {
  let getquery = gQuery(page);
  // console.log("get quety: ", getquery);
  // console.log("set quety: ", page);

  history.replaceState({}, "", getquery);
}
export default query;
function gQuery(kv) {
  let search = "?";
  const currentQuery = { ...query(), ...kv };
  for (const key in currentQuery) {
    search += `${key}=${currentQuery[key]}&`;
  }
  search = search.slice(0, search.length - 1);
  return search;
}
