<script>
import { ref } from "@vue/reactivity";
export default {
  name: "main-money",
  setup() {
    const visiblity = ref(false);
    const doneNum = ref(10);
    const undoNum = ref(20);
    const doableNum = ref(18);
    const withdraw = ref(1500);
    const unwithdraw = ref(500);
    const money = ref(2000);
    return {
      doneNum,
      undoNum,
      doableNum,
      withdraw,
      unwithdraw,
      money,
      visiblity,
    };
  },
  inject: ["setButtonSize"],
  methods: {
    toggle() {
      this.visiblity = !this.visiblity;
    },
    open() {
      this.$emit("toggle");
      this.$bus.$emit("toggleShadow");
    },
  },
  computed: {
    graphStyle() {
      return "transform:rotate(135deg)";
    },
    perent() {
      return (this.withdraw / (this.withdraw + this.unwithdraw)) * 360;
    },
    showMoney() {
      if (this.visiblity) {
        return this.money;
      } else {
        return "â€¢".repeat(("" + this.money).length);
      }
    },
